@media screen and (orientation: landscape) {
    .home-container {
        width: 100vw;
        height: 100vh;
        overflow: hidden;
        display: grid;
        grid-template-columns: 100%;
        grid-template-rows: 80% 20%;
    }
    .main-wrapper {
        grid-column: 1 / 2;
        grid-row: 1 / 2;
        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .button-wrapper {
        grid-row: 2 / 3;
        grid-column: 1 / 2;
        display: flex;
        align-items: center;
        justify-content: space-around;
        padding: 0 20%;
    }
    .big-button {
        font-size: calc(1vmin + 10px);
    }
    .card-div {
        color: black;
        background-color: #FFFFFFDD;
        width: 35%;
        height: 35%;
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
        border: 2px solid black;
        border-radius: 25px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-direction: column;
        padding: 3vh 0;
        box-sizing: border-box;
        transition: all 0.45s ease-in-out;
    }
    .card-div h3 {
        font-size: calc(1.2vw + 10px);
    }
    .card-div h1 {
        font-size: calc(1.7vw + 10px);
    }
    .changed-recipe {
        transform: scale(1.1);
        box-shadow: 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22);
    }
}

@media screen and (orientation: portrait) {
    .home-container {
        width: 100vw;
        height: 100vh;
        overflow: hidden;
        display: grid;
        grid-template-columns: 100%;
        grid-template-rows: 20% 80%;
    }
    .main-wrapper {
        grid-column: 1 / 2;
        grid-row: 2 / 3;
        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .button-wrapper {
        grid-row: 1 / 2;
        grid-column: 1 / 2;
        display: flex;
        align-items: center;
        justify-content: space-around;
        padding: 0 8%;
    }
    .card-div {
        color: black;
        background-color: #FFFFFFDD;
        width: 70%;
        height: 35%;
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
        border: 2px solid black;
        border-radius: 25px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-direction: column;
        padding: 3vh 0;
        box-sizing: border-box;
        transform: translateY(-10vh);
    }
    @media screen and (min-width: 700px) {
        .big-button {
            font-size: calc(1vmin + 10px);
        }
    }
}